*,
h1,
h2,
h3,
h4,
h5,
h6 {
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body {
    background-color: #fafafa;
    font-family: 'Roboto', sans-serif;
}

.label{
    position: relative;
    text-align: center;
    top: 50%;
}

.todo {
    position: absolute;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    width: 530px;
    height: 500px;
    background-color: #fff;
    -webkit-box-shadow: 0px 9px 24px rgba(0, 0, 0, 0.01), 0px 25px 40px rgba(0, 0, 0, 0.01);
    box-shadow: 0px 9px 24px rgba(0, 0, 0, 0.01), 0px 25px 40px rgba(0, 0, 0, 0.01);
    border-radius: 6px;
    overflow: hidden;
    border: 1px solid #ebebeb;
}

.todo__list{
    overflow: auto;
    height: calc(100% - 136px);
}



.todo__header {
    background-color: #ff4f3e;
    padding: 20px 30px;
}

.todo__header h4 {
    color: #fff;
    font-size: 22px;
    font-weight: 500;
}

.todo__add-field {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    border-bottom: 1px solid #f4f4f4;
}

.todo__add-field input {
    border: 0;
    font-size: 18px;
    padding: 20px;
    outline: none;
    -webkit-box-flex: 1;
    -ms-flex: 1;
    flex: 1;
}

.todo__add-field input::-webkit-input-placeholder {
    color: #bfbfbf;
}

.todo__add-field input:-ms-input-placeholder {
    color: bfbfbfe;
}

.todo__add-field input::-ms-input-placeholder {
    color: #bfbfbf;
}

.todo__add-field input::-moz-placeholder {
    color: #bfbfbf;
}

.todo__add-field input::placeholder {
    color: #bfbfbf;
}

.todo__add-field-button {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    border: 0;
    color: transparent;
    height: 65px;
    width: 65px;
    background-color: transparent;
    outline: none;
    cursor: pointer;
}

.todo__add-field-button svg {
    fill: #bfbfbf;
}

.todo__add-field-button:hover {
    background-color: #fbfbfb;
}

.todo__add-field-button:hover svg {
    fill: #ff9696;
}

.todo__list-item {
    border-bottom: 1px solid #f4f4f4;
    padding: 20px 20px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
}

.todo__list-item--completed {
    text-decoration: line-through;
}

.todo__list-item--completed .todo__list-item-check svg {
    display: block;
}

.todo__list-item--completed .todo__list-item-check,
.todo__list-item--completed .todo__list-item-check:hover {
    background-color: #db4c3f;
}

.todo__list-item p {
    font-size: 18px;
    -webkit-box-flex: 1;
    -ms-flex: 1;
    flex: 1;
}

.todo__list-item-remove {
    cursor: pointer;
    -webkit-transition: fill .2s ease-in-out;
    -o-transition: fill .2s ease-in-out;
    transition: fill .2s ease-in-out;
}

.todo__list-item-remove:hover {
    fill: #f7a2a2;
}

.todo__list-item-check {
    width: 24px;
    height: 24px;
    border: 2px solid #db4c3f;
    border-radius: 30px;
    margin-right: 15px;
    cursor: pointer;
    -webkit-transition: background-color .2s ease-in-out;
    -o-transition: background-color .2s ease-in-out;
    transition: background-color .2s ease-in-out;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
}

.todo__list-item-check svg {
    fill: #fff;
    width: 10px;
    height: 10px;
    display: none;
}

.todo__list-item-check:hover {
    background-color: #ff9696;
}